import React, { Component } from "react"
import MetaTags from 'react-meta-tags';

import { format } from 'date-fns';
import {
  Card,
  Col,
  Container,
  Row,
  CardBody,
  CardHeader,
  CardTitle,
  FormGroup,
  Label,
  Button,
  Form,
  Input,
  InputGroupAddon,
  InputGroupText,
  InputGroup,
  Breadcrumb,
  BreadcrumbItem,
  Spinner,
 
  

} from "reactstrap"
import Select from "react-select"
import axios from 'axios'
import  { Redirect } from 'react-router-dom'
// import $ from "jquery";
import { French } from "flatpickr/dist/l10n/fr.js";
import Flatpickr from "react-flatpickr"
import ReactSelect from 'react-select'
import makeAnimated from 'react-select/animated';
//import BootstrapSelect from "bootstrap-select"
import url from "../../common/api"
import {getLanguageList} from "../../services/langueService.js"
import {getThematiqueList} from "../../services/thematiqueService.js"

import {getUniteAdministrativeList} from "../../services/uniteAdministrativeService.js"

import {getTypeSystemeIrrigueList} from "../../services/systemeIrrigueService.js"
import {getTypeEvenementList} from "../../services/typeEvenementService.js"
import {getPaysList} from "../../services/paysService.js"
import {getStructureList} from "../../services/structureService.js"
import {getCategorieList} from "../../services/categorieService.js"


class DetailEvenement extends Component {

  constructor(props) {
    super(props)

    this.state = { ok:"",evenement:{},systeme:[],unite:[],langue:[],thematique:[],
    mot:[],video:"",image:[],pjointe:[],localite:[],
    type_evenements:[],event:"",checkSysteme:[],irrigues:[],systeme_data:[],checkUnite:[],
    unites:[],unite_data:[],thematiques:[],thematique_data:[],checkThematique:[],chargement:false,hide:"none",
    validate_update:false,
    chargement_data:false, 
    accept_update:true,
    type:props.type, index:props.index,
     }
     this.handleCheckSysteme.bind(this)
     this.handleCheckUnite.bind(this)
     this.handleCheckThematique.bind(this)
  }



  handleDroit=()=>{
   $(".owener-close").css("display","none")
   //Type evenement
   $("#show-type-evenement").css("display","none")
   $("#valideRessource").css("display","none")
   $("#show-ok-te").css("display","none")

   //Tableaux
   $("#typesystemeirrigation").css("display","none")
   $("#thematique").css("display","none")
   $("#uniteadministrative").css("display","none")

   $("#show-tsi").css("display","none")
   $("#show-thematique").css("display","none")
   $("#show-ua").css("display","none")
  }

  componentDidMount(){
      //   const requestOptions = {
  //     method: 'GET',
  //     headers: { 'Content-Type': 'application/json','Accept': 'application/json' },
  //     body: JSON.stringify(data)
  // };

    //Date

    this.handleCharge()
 

    var id=localStorage.getItem("idEvenement")
 
    // this.handleChargeSysteme()
    // this.handleChargeThematique()
    // this.handleChargeUnite()

    this.handleChargeLangue()
    this.handleChargeUnite()
    this.handleChargeSysteme()
    this.handleChargeThematique()
    this.handleTypeEvenement()


    this.getData()
  }

  getData=()=>{
    //this.setState({chargement:false})
    var id=localStorage.getItem("idEvenement")
    fetch(url+"evenements/"+id)
    .then((response) => response.json())
    .then((data) => {
      this.setState({chargement:true})
      this.setState({evenement:data})
      console.log("la ressource:",data)
      this.setState({systeme:data.type_systeme_irrigues})
      this.setState({unite:data.unite_administratives})
      this.setState({thematique:data.thematiques})
    
      
      this.handleDroit()

      if(this.state.index===true){
        $("#droit").hide()
      }
      
      $("#libelle").val(data.libelle)
      $("#url").val(data.url)
      $("#description").val(data.description)
      $("#lieu").val(data.lieu)
      $("#objectif").val(data.objectif)
      $("#cible").val(data.cible)
      $("#organisateur").val(data.organisateur)
      $("#date_debut").val(data.date_debut)
      $("#date_fin").val(data.date_fin)
     // this.setState({chargement:true})
   
      try{
          $("#type_evenement").val(data.type_evenement.libelle)
          this.setState({event:data.type_evenement.libelle})
         }catch(e){
 
         }
    })

  }

  handleRetour=()=>{
    if(this.state.index===true){
      $('#block_detail_ressource').hide()
      $('#block_page_acceuil').show()
      return 
  }else{
    this.setState({ok: <Redirect to='/liste-calendar'  />});
  return
  }
    
  }








patch=(data)=>{
  var id=localStorage.getItem("idEvenement")
  const requestOptions = {
      method: 'PATCH',
      headers: { 'Content-Type': 'application/json','Accept': 'application/json' },
      body: JSON.stringify(data)
  };

    fetch(url+"evenements/"+id, requestOptions)
    .then((response) => response.json())
    .then(response =>{console.log("ok,",response)
    this.getData()
   
   } ,
   (error) => {
     
   })
}


//charge

handleChargeLangue=()=>{
      
  getLanguageList().then(e=>this.setState({langues:e}))
 
}

handleChargeUnite=()=>{
    
  getUniteAdministrativeList().then(e=>this.setState({unites:e}))

 
}

handleTypeEvenement=()=>{
  getTypeEvenementList().then(e=>this.setState({type_evenements:e}))
}

handleChargeSysteme=()=>{

  getTypeSystemeIrrigueList().then(e=>this.setState({irrigues:e}))
 
}


handleChargeThematique=()=>{

  getThematiqueList().then(e=>this.setState({thematiques:e}))
    
}

//filter 
handleThematique=()=>{
  //this.handleChargeThematique()
    
  let auteur1 = this.state.thematique.map(val => { return val.id});
 
  let auteur2 = this.state.thematiques.filter(itemX => !auteur1.includes(itemX.value));
 
  this.setState({thematiques:auteur2})
  $("#thematique").show()
  $("#no-show-thematique").hide()
  $("#show-thematique").show()
}


handleUnite=()=>{
 // this.handleChargeLocalisation()
 let pay1 = this.state.unite.map(val => { return val.id});
 
 let pay2 = this.state.unites.filter(itemX => !pay1.includes(itemX.value));

 this.setState({unites:pay2})
  $("#localisation").show()
  $("#no-show-localisation").hide()
  $("#show-localisation").show()
}

handleLangue=()=>{
  //this.handleChargeLangue()
  let langue1 = this.state.langue.map(val => { return val.id});
 
  let langue2 = this.state.langues.filter(itemX => !langue1.includes(itemX.value));
 
  this.setState({langues:langue2})
  $("#langue").show()
  $("#no-show-langue").hide()
  $("#show-langue").show()
}

handleTypeSystemeIrriguation=()=>{
  //this.handleChargeSysteme()

  let auteur1 = this.state.systeme.map(val => { return val.id});
  
 
   
  let auteur2 = this.state.irrigues.filter(itemX => !auteur1.includes(itemX.value));

 

  this.setState({irrigues:auteur2})
  
}

handleCheckSysteme=(data)=>{
  var  tab=[]
  this.setState({checkSysteme:tab})
  try{
      data.map((val,idx)=>{
          this.setState({checkSysteme:[...this.state.checkSysteme,{type_systeme_irrigue_id:val.value}]})
      })

  }catch(error){
      
  }

  }

  handleCheckUnite=(data)=>{
    var  tab=[]
    this.setState({checkUnite:tab})
    try{

        data.map((val,idx)=>{
            this.setState({checkUnite:[...this.state.checkUnite,{unite_administrative_id:val.value}]})
        })

    }catch(error){
        
    }
    
    }

    handleCheckThematique=(data)=>{
      var  tab=[]
      this.setState({checkThematique:tab})
  
      try{
         
          data.map((val,idx)=>{
              this.setState({checkThematique:[...this.state.checkThematique,{thematique_id:val.value}]})
          })
  
      }catch(error){
          
      }
     
      }

handleUpdateSysteme=()=>{
     
  this.patch({"evenement_systeme_irrigues_attributes":this.state.checkSysteme})
  $("#typesystemeirrigation").hide()
  $("#no-show-tsi").show()
  $("#show-tsi").hide()
}
  handleUpdateUnite=()=>{
      
  this.patch({"evenement_unite_administratives_attributes":this.state.checkUnite})
    
  
  $("#uniteadministrative").hide()
  $("#no-show-ua").show()
  $("#show-ua").hide()
  }
  handleUpdateThematique=()=>{
    this.patch({"evenement_thematiques_attributes":this.state.checkThematique})

        $("#thematique").hide()
        $("#no-show-thematique").show()
        $("#show-thematique").hide()
  } 

  handleCharge=()=>{}

  handleModifierEvent=()=>{
    this.setState({accept_update:false})
     
    this.handleTypeSystemeIrriguation()
    this.handleUnite()
   
    this.handleThematique()


    setTimeout(()=>
    this.setState({accept_update:true})
  , 2000)

     if(this.state.accept_update){
      $(".owener-close").hide()
      $("#droit").hide()
      $(".owener").show()
      $(".show").show()
      this.setState({hide2:"none"})
      this.setState({hide:"block"})

      document.getElementById("libelle").readOnly = false;
      document.getElementById("organisateur").readOnly = false;
      document.getElementById("description").readOnly = false;
      document.getElementById("cible").readOnly = false;
      document.getElementById("objectif").readOnly = false;
      document.getElementById("lieu").readOnly = false;
      document.getElementById("url").readOnly = false;
      
    }
  }  

  handleAnuler=()=>{
    this.setState({validate_update:false,
      accept_update:true,
      checkSysteme:[],
      checkThematique:[],
      checkUnite:[],
      checkLangue:[],
      checkLocalisation:[],
      checkMot:[],
      checkNewAuteur:[],
      checkSousTypeRessource:[],
      checkNewMot:[],
      checkAcces:[],
      hide:"none",
      remove_data:{}})
    // this.handleChargeAll()
    this.getData()
    $(".owener-close").show()
    $("#droit").show()
    $(".owener").hide()
    $(".show").hide()
    document.getElementById("libelle").readOnly = true;
    document.getElementById("organisateur").readOnly = true;
    document.getElementById("description").readOnly = true;
    document.getElementById("cible").readOnly = true;
    document.getElementById("objectif").readOnly = true;
    document.getElementById("lieu").readOnly = true;
    document.getElementById("url").readOnly = true;
  }
  
 
 render() {

  const animatedComponents = makeAnimated();

  const dataTest = [
    {
      value: 1, 
      label:"Test 1"
    },
    {
      value:2, 
      label:"Test 2"
    },
    {
      value:3, 
      label:"Test 3"
    }
  ];

  return (
          <React.Fragment>
          {
            (this.state.chargement)?
                (<>
              
            <div className="page-content">
            
               {this.state.ok}

            <MetaTags>
                <title>Détails de l'évenement</title>
            </MetaTags>
            <Container fluid={true}>
            <Row>
            {!this.state.validate_update?
                  <Col md="6">
                    <Button onClick={this.handleRetour} style={{backgroundColor:"#6A7172",float:"left",borderRadius:"10px"}} >
                      Retour
                    </Button>
                  </Col>:null
                }

                  <Col md="6">
                  <div id="droit">
                  <Button onClick={this.handleModifierEvent} style={{backgroundColor:"#aad29a", borderColor:"#aad29a",float:"right",borderRadius:"10px"}}  >
                      Modifier l'évenement
                    </Button>
                  </div>
                  {
                    !this.state.accept_update?
                    <Button  style={{backgroundColor:"#aad29a", borderColor:"#aad29a",float:"right",borderRadius:"10px"}}  >
                  <span className="spinner-border spinner-border-sm" role="status" aria-hidden="true">...</span>
                   
                 </Button>:null
                   }
                  </Col>
                  
                  
                </Row>
                {this.state.validate_update?
                <Row>
                  <Col md="6">
                  <Button onClick={this.handleRetour} style={{backgroundColor:"#6A7172",float:"left",borderRadius:"10px"}} >
                      Retour
                    </Button>
                  </Col>
                  <Col md="6" >
                  
                    
                    {
                    this.state.chargement_data?
                    <Button id="chargement" style={{backgroundColor:"#aad29a", borderColor:"#aad29a",float:"right",borderRadius:"10px"}}  >
                        <span className="spinner-border spinner-border-sm" role="status" aria-hidden="true"> </span>
                          Envoi...
                    </Button>:
                    <Button id="initial" onClick={this.handleModifierEvenement} style={{backgroundColor:"#aad29a", borderColor:"#aad29a",float:"right",borderRadius:"10px"}}  >
                       Appliquer la modification
                    </Button>
                    }
                    
                  </Col>
                </Row>:null
                  }
             
                {/* <Row>
                  <Col md="8">
                      <div className="page-title-box d-flex align-items-center justify-content-between">
                      <h4 className="mb-0 font-size-18">Détails de l'évenement</h4>
                      </div>
                  </Col>
                </Row> */}
     
                <hr/>

                <Card style={{borderRadius:"25px",borderColor:"#6A7172",borderWidth:"5px"}}>
                  <CardBody>

                  {/* <Row>
                    <Col md="6">
                      <Button onClick={this.handleRetour} style={{backgroundColor:"#6A7172",float:"left",borderRadius:"10px"}} >
                        Retour
                      </Button>
                    </Col>
                    <Col md="6" id="droit">
                      <Button onClick={this.handleModifierEvenement} style={{backgroundColor:"#aad29a", borderColor:"#aad29a",float:"right",borderRadius:"10px"}}  color="success">
                        Modifier l'évenement
                      </Button>
                    </Col>
                  </Row>
                  <hr/> */}
                  <Row className="mt-4"> 
                    <Col md="8">
                    <label>Libellé</label>
                      
                      <Input
                            className="form-control"
                            type="text"
                            id="libelle"
                      
                            readOnly
                        />
                    </Col>

                    <Col md="4">
                     <label >Organisateur</label>
                     
                      <Input
                        className="form-control"
                        type="text"
                        id="organisateur"
                        onBlur={this.handleUpdateOrganisateur}
                        readOnly
                      />
                    </Col>
                  </Row>
                  
                  <Row className="mt-4"> 
                    <Col md="12" > 
                     <label>Description </label>
                    
                      <textarea  className="form-control"   id="description" readOnly/>
                    </Col>
                  </Row>

                  <Row className="mt-4"> 
                      

                    <Col md="4">
                    <label>Objectif</label>
                     
                      <textarea
                        className="form-control"
          
                        id="objectif"
                     
                        readOnly
                      />
                    </Col>

                    <Col md="4">
                    <label >Cible</label>
                     
                      <Input
                            className="form-control"
                            type="text"
                            id="cible"

                            readOnly
                        /> 
                    </Col>

                    <Col md="4">
                    <label>Lieu</label>
                   
                      <Input
                            className="form-control"
                            type="text"
                            id="lieu"
                           
                            readOnly
                        /> 
                    </Col>

                    
                  </Row>

                  

                  

                  <Row className="mt-4"> 
                      <Col md="4">
                        <label>Types D'évenement</label>
                
                        <Input
                            className="form-control owener-close"
                            type="text"
                            id="type_evenement"
                            readOnly
                        />

                        <select className="form-select show" aria-label="Default select example" id="show-type-evenement">
                        <option value="0" selected>{this.state.event}</option>
                        {
                            this.state.type_evenements.filter(e=>e.libelle!==this.state.event).map(
                                (element,idx) =>(
                                    <option value={element.id}>{element.libelle}</option>
                                )
                            )
                        }
                        </select>
                       
                      </Col>

                      <Col md="4">
                        <div id="ddd" >
                        <label>Date de débutt</label>
                          
                          <Input
                              className="form-control"
                              type="text"
                              id="date_debut"
                            
                              readOnly
                          />
                        </div>

                     
                       
                      </Col>


                    <Col md="4">
                        <div id="ddf" >
                          <label >Date de fin</label>
                       
                          <Input
                              className="form-control"
                              type="text"
                              id="date_fin"
                              onBlur={this.handleUpdateDateFin}
                              readOnly
                          />
                    
                      </div>
                    </Col>
                  </Row>

                  <Row className="mt-4"> 
                    <Col md="12"> 
                    <label >Url</label>
                     
                        <Input
                            className="form-control"
                            type="text"
                            id="url"
                            readOnly
                        /> 
                    </Col>
                  </Row>
                  </CardBody>
                </Card>


                <Row className="mb-4">
                    <Col md="6">
                        <Card style={{borderRadius:"25px",borderColor:"#6A7172",borderWidth:"5px"}}>
                        <CardBody>
                          <CardTitle className="mb-4 h4">Type(s) de système(s) irrigué(s)</CardTitle>
                         
                          <div id="typesystemeirrigation" className="show mt-4">
                              <ReactSelect
                                  closeMenuOnSelect={false}
                                  components={animatedComponents}
                                  isMulti
                                  onChange={this.handleCheckSysteme}
                                  options={this.state.irrigues}
                                  placeholder="Choisir la/les type(s) de système(s) irrigué(s)"
                              />
                          </div>
                          <div className=" mt-4">

                            <table className="table align-middle">
                                <thead>
                                    <tr>
                                        <th>Libelle</th> <th>Sigle</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {
                                        (this.state.systeme.length!=0)?

                                        (this.state.systeme.map(
                                            (element,idx)=>(
                                                <tr>
                                                <td style={{ width: "30%" }}>
                                                    <p className="mb-0">{element.libelle}</p>
                                                </td>
                                                <td style={{ width: "30%" }}>
                                                    <p className="mb-0">{element.sigle}</p>
                                                </td>
                                            </tr>
                                            )

                                        )):(

                                            <tr>
                                            <td style={{ width: "30%" }}>
                                                <p className="mb-0">Néant</p>
                                            </td>
                                            <td style={{ width: "30%" }}>
                                                <p className="mb-0">Néant</p>
                                            </td>
                                        </tr>
                                        )

                                    }                              


                                </tbody>
                            </table>
                            </div>
                        </CardBody>
                        </Card>
                    </Col>
                    
                    <Col md="6">
                        <Card style={{borderRadius:"25px",borderColor:"#6A7172",borderWidth:"5px"}}>
                        <CardBody>
                          <CardTitle className="mb-4 h4">Unité(s) administrative(s)</CardTitle>
                        
                          <div id="uniteadministrative" className="show mt-4">
                              <ReactSelect
                                  closeMenuOnSelect={false}
                                  components={animatedComponents}
                                  isMulti
                                  onChange={this.handleCheckUnite}
                                  options={this.state.unites}
                                  placeholder="Choisir la/les unité(s) administrative(s)"
                              />
                          </div>

                          <div className=" mt-4">
                            <table className="table align-middle">

                            <thead>

                                    <tr>
                                        <th>Libelle</th> <th>Code</th> 
                                    </tr>
                                </thead>
                                <tbody>

                                    
                                {
                                (this.state.unite !=0) ?
                                (this.state.unite.map(
                                    (element,idx)=>(
                                        <tr>
                                        <td style={{ width: "30%" }}>
                                            <p className="mb-0">{element.libelle}</p>
                                        </td>
                                        <td style={{ width: "30%" }}>
                                            <p className="mb-0">{element.code}</p>
                                        </td>
                                    </tr>
                                    ))):(

                                        <tr>
                                        <td style={{ width: "30%" }}>
                                            <p className="mb-0">Néant</p>
                                        </td>
                                        <td style={{ width: "30%" }}>
                                            <p className="mb-0">Néant</p>
                                        </td>
                                    </tr>
                                    )

                                

                                } 


                                </tbody>
                            </table>
                            </div>
                            </CardBody>
                        </Card>
                    </Col>
                </Row>

                <Row>
                <Col md="6">
                        <Card style={{borderRadius:"25px",borderColor:"#6A7172",borderWidth:"5px"}}>
                        <CardBody>
                         <CardTitle className="mb-4 h4">Thématique(s)</CardTitle>
                          <div id="thematique" className="show mt-4">
                              <ReactSelect
                                  closeMenuOnSelect={false}
                                  components={animatedComponents}
                                  isMulti
                                  onChange={this.handleCheckThematique}
                                  options={this.state.thematiques}
                                  placeholder="Choisir la/les thématique(s)"
                              />
                          </div>

                          <div className=" mt-4">
                            <table className="table align-middle">

                            {/* <thead>
                                    <tr>
                                        <th>Libelle</th>
                                    </tr>
                                </thead> */}
                                <tbody>
                                    
                                    {
                                    (this.state.thematique.length!=0)?
                                    (this.state.thematique.map(
                                        (element,idx)=>(
                                            <tr>
                                            <td style={{ width: "30%" }}>
                                                <p className="mb-0">{element.libelle}</p>
                                            </td>
                                            
                                        </tr>
                                        )
                                       )
                                    ):(  <tr>
                                        <td style={{ width: "30%" }}>
                                            <p className="mb-0">La liste est vide</p>
                                        </td>
                                        
                                    </tr>)

                                    } 


                                </tbody>
                            </table>
                            </div>
                            </CardBody>
                        </Card>
                    </Col>
                    

                    {/* <Col md="6">

                        <Card>
                        <CardBody>
                            <CardTitle className="mb-4 h4">Localisation(s)</CardTitle>
                        <div className=" mt-4">
                            <table className="table align-middle">
                                <thead>
                                    <tr>
                                        <th>Code de la localité</th> <th>Nom de la localité</th>
                                    </tr>
                                </thead>
                                <tbody>
                                </tbody>
                            </table>
                            </div>
                        </CardBody>
                        </Card>

                    </Col> */}
                </Row>

                
                {this.state.validate_update?
                <Row>
                  <Col md="6">
                    <Button onClick={this.handleAnuler} style={{backgroundColor:"#6A7172",float:"left",borderRadius:"10px"}} >
                      Annuler
                    </Button>
                  </Col>
                  <Col md="6" >
                  
                    
                    {
                    this.state.chargement_data?
                    <Button id="chargement" style={{backgroundColor:"#aad29a", borderColor:"#aad29a",float:"right",borderRadius:"10px"}}  >
                        <span className="spinner-border spinner-border-sm" role="status" aria-hidden="true"> </span>
                          Envoi...
                    </Button>:
                    <Button id="initial" onClick={this.handleUpdateEvent} style={{backgroundColor:"#aad29a", borderColor:"#aad29a",float:"right",borderRadius:"10px"}}  >
                       Appliquer la modification
                    </Button>
                    }
                    
                  </Col>
                </Row>:null
                  }

                <hr></hr>
                

                
              </Container>
              </div>
                  </>):(
                    <div style={{marginTop:"250px",textAlign:"center"}}>
                       <Spinner type="grow" color="secondary" />
                </div>
                )
                
              }
          </React.Fragment>
        )}
  }
  export default DetailEvenement